.services-block-four .inner-box h3 {
  position: relative;
  font-size: 18px;
  font-weight: 600;
  text-transform: capitalize;
}
.services-block-four .inner-box .text {
  font-size: 14px;
  color: #888;
  margin-top: 8px;
  margin-bottom: 10px;
}
.light-version p {
  color: #888;
}
.contant-p{
  line-height: 2rem;
}
.dream-btn {
  position: relative;
  z-index: 1;
  min-width: 160px;
  height: 48px;
  line-height: 48px;
  font-size: 12px;
  font-weight: 600;
  letter-spacing: 1px;
  display: inline-block;
  padding: 0 20px;
  text-align: center;
  text-transform: uppercase;
  background-size: 200% auto;
  color: #fff;
  box-shadow: 0 3ox 20px rgba(0, 0, 0, 0.1);
  border-radius: 7px;
  background: rgb(62, 148, 228);
  -webkit-transition: all 500ms;
  transition: all 500ms;
}
.btn {
  border: 2px solid #007bff;
}
.btn:not(:disabled):not(.disabled) {
    cursor: pointer;
}