* {
  margin: 0px;
}
.home-page {
  /* background: yellow; */

  height: 100vh;
}
.home-page-container {
  /* background: blue; */

  height: 100%;
}
.home-page-container1 {
  /* background: gray; */

  height: 85%;

  display: flex;
}
.left-cont1 {
  /* background: green; */

  /* left: 10%; */

  width: 48%;

  display: flex;

  flex-direction: column;

  justify-content: center;

  align-items: center;

  margin-left: 2%;

  margin-top: 8%;
}
.left-cont1 h1 {
  font-size: 45px;

  font-weight: 600;

  margin-bottom: 20px;

  color: #13287e;
}
.left-cont1 p {
  line-height: 2rem;
  font-size: 25px;
  color: #8888a7;
}
.right-cont1 {
  width: 50%;
  margin-top: 5%;

  background: aliceblue;
  /* background-image: url("./public/banner2.png"); */
}
.right-cont1 img {
  height: 100%;
  width: 100%;
}
.left-cont1-btns {
  margin-top: 3%;
  height: 10%;
  width: 100%;
  /* border-radius: 1rem; */
  display: flex;
  /* justify-content: space-evenly; */
}
.home-btn {
  background: #3e94e4;
  height: 100%;
  width: 35%;
  border-radius: 8px;
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
}
.home-btn a {
  color: white;
  font-size: 18px;
  text-decoration: none;
}

